import { Component, OnInit, Output } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MarkdownService } from 'ngx-markdown';

import {
  ElementRef,
  ViewChild,
  TemplateRef
} from '@angular/core';

import { ArticlesService } from '../services/articles/articles.service';

@Component({
  selector: 'app-articles',
  templateUrl: './articles.component.html',
  styleUrls: ['./articles.component.scss']
})
export class ArticlesComponent implements OnInit {
  article: object = {};
  // @Output() articleHeads: Array<any> = [];
  articleHeads: string = '';

  @ViewChild(TemplateRef, { read: false, static: true }) template: TemplateRef<any>;
  @ViewChild('panel', { read: false, static: true }) panel: ElementRef;

  constructor(
    private articlesService: ArticlesService,
    private route: ActivatedRoute,
    private markdownService: MarkdownService
  ) {
    const id = this.route.snapshot.paramMap.get('id');
    this.articlesService.getArticle(id).subscribe((res: any) => this.article = res.data);
  }

  ngOnInit() {
    this.renderer();
  }

  transclusion(article) {
    if (article.contentType && article.content) {
      this.articlesService.getMdContent(article.content).subscribe(
        content => this.article = { content }
      )
    }
  }

  /**
   * overload renderer
   */
  renderer() {
    this.markdownService.renderer.heading = (text: string, level: number) => {
      const escapedIndex = text.toLocaleLowerCase().replace(/[^\w]+/g, '-');
      // this.articleHeads.push({ level, name: escapedIndex, text });
      this.articleHeads += `<li class="header-${level}"><a href="#${escapedIndex}">${text}</a></li>`

      return `<h${level}>
                <a class="anchor" name="${escapedIndex}" href="#${escapedIndex}">
                  <span class="header-link"></span>
                </a>${text}
              </h${level}>`;
    }
  }
}
