import { Injectable } from '@angular/core';
import { Observable, of, pipe } from 'rxjs'
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { NAVLIST, Nav } from '../../mock/navigation';

@Injectable({
  providedIn: 'root'
})
export class NavigationService {

  constructor(
    private http: HttpClient
  ) { }

  private navsUrl = 'localhost:3001/api/navigation';

  getNavigations(): Observable<Nav[]> {
    // return of(NAVLIST);
    return this.http.get<Nav[]>(this.navsUrl);
  }

}
